.navBar {
    background-color: #03A9F4;
    color: #fff;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 6vh;
    width: 100%;
    padding: 0 5vw;
    gap: 5vh;
}

.navMenu {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 12px;
}

.navMenu > li {
    list-style: none;
}

.navItems {
    transition-duration: .4s;
    height: 100%;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    text-decoration: none;
    color: white;
    border-bottom: 3px solid transparent;
}

.navItems:hover {
    border-color: #CDDC39
}